<template>
    <div class="nav animate__animated" :class="{ 'animate__slideInDown': slideDown, 'animate__slideOutUp': slideOutUp }"
        :style="{ 'animation-duration': '500ms' }">
        <div class="content">
            <div>
                <svg-icon class="logo" icon-class="logo" />
            </div>
            <div class="tab">
                <p v-for="(item, index) in items" :key="index" @click="fnGO(item.path)">{{ item.name }}</p>
                <svg-icon class="google" icon-class="google" />
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props: ["slideDown", "slideOutUp"],
    data() {
        return {
            items: [
                {
                    name: 'inicio',
                    path: '/'
                },
                {
                    name: 'aviso de privacidad',
                    path: '/aviso'
                },
                {
                    name: 'blog',
                    path: '/blog'
                },
                {
                    name: 'ayuda',
                    path: '/help'
                }
            ]
        }
    },
    methods: {
        fnGO(path) {
            console.log("当前路径", path)
            switch (path) {
                case '/':
                    if (this.$route.path !== '/') {
                        this.$router.push('/')
                    }
                    break
                case '/aviso':
                    if (this.$route.path !== '/aviso') {
                        this.$router.push('/aviso')
                    }
                    break
                case '/blog':
                    if (this.$route.path !== '/blog') {
                        this.$router.push('/blog')
                    }
                    break
                case '/help':
                    if (this.$route.path !== '/help') {
                        this.$router.push('/help')
                    }
                    break
                default:
            }
        }
    }
}
</script>
<style lang="less" scoped>
.nav {
    background: rgba(244, 244, 244, 0.8);
    // opacity: 0.2;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    // width: 100vw;
    padding: 0 52px;
    height: 109px;
    &:hover {
        cursor: pointer;
    }

    .content {
        width: calc(100vw - 104px);
        height: 108px;
        border-bottom: 1px solid #111312;
        display: flex;
        justify-content: space-between;

        .logo {
            display: inline-block;
            width: 148px;
            height: 64px;
            margin-top: 17px;
        }

        .tab {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 670px;

            p {
                // height: 25px;
                font-family: Inter, Inter;
                font-weight: 600;
                font-size: 23px;
                color: #000000;
                line-height: 27px;
                text-align: left;
                font-style: normal;

                &:hover {
                    color: #8292F4;
                }
            }

            .google {
                display: inline-block;
                width: 196px;
                height: 56px;
            }
        }
    }
}
</style>